
window.onscroll = function (){
	var navbar = document.getElementById('header-stick');
	var about = document.getElementById('about');
	var sticky = navbar.offsetTop;
	if (window.pageYOffset >= sticky) {
		navbar.classList.add('sticky'),
		about.classList.add('sticky')
	} else {
		navbar.classList.remove('sticky'),
		about.classList.remove('sticky');
	}
}

var mask = document.querySelector('.mask'),
	vacancyModal = document.getElementById('contacts-right-column'),
	vacancyBtn = document.getElementById('vacancy-btn'),
	modalW = document.getElementById('modalW'),
	modalWClose = document.getElementById('close');

modalWClose.addEventListener('click', function() {
	mask.classList.remove('active'),
	modalW.classList.remove('active');
})

//Show modal window on click button at the head
document.getElementById('showModal').addEventListener('click', function(){
	mask.classList.add('active'),
	modalW.classList.add('active')
});

mask.addEventListener('click', function(){
	mask.classList.remove('active'),
	vacancyModal.classList.remove('active'),
	vacancyBtn.classList.remove('active'),
	modalW.classList.remove('active');
});

vacancyBtn.addEventListener('click', function () {
	mask.classList.remove('active'),
	vacancyBtn.classList.remove('active'),
	vacancyModal.classList.remove('active');
});

//Show modal for Vacancy
let round = document.querySelectorAll('.round');
round.forEach((round) => round.addEventListener('click', function () {
	mask.classList.add('active');
	vacancyModal.classList.add('active'),
	vacancyBtn.classList.add('active');
}));

//Prop for Button on ModalWindow
$('#customCheck').on('change', function () {
	if ($('#customCheck').prop('checked')) {
		$('#submit').attr('disabled', false).addClass('active');
	} else {
		$('#submit').attr('disabled', true).removeClass('active');
	}
});

function closeModal() {
	document.querySelector('.contacts-right-column').classList.remove('active'),
	mask.classList.remove('active'),
	vacancyModal.classList.remove('active'),
	vacancyBtn.classList.remove('active'),
	modalW.classList.remove('active');
};

$(document).keyup(function (e) {
	if (e.keyCode == 27) {
		closeModal();
	}
});

$('img.svg').each(function () {
	var $img = $(this);
	var imgID = $img.attr('id');
	var imgClass = $img.attr('class');
	var imgURL = $img.attr('src');

	$.get(imgURL, function (data) {
		// Get the SVG tag, ignore the rest
		var $svg = $(data).find('svg');

		// Add replaced image's ID to the new SVG
		if (typeof imgID !== 'undefined') {
			$svg = $svg.attr('id', imgID);
		}
		// Add replaced image's classes to the new SVG
		if (typeof imgClass !== 'undefined') {
			$svg = $svg.attr('class', imgClass + ' r-svg');
		}

		// Remove any invalid XML tags as per http://validator.w3.org
		$svg = $svg.removeAttr('xmlns:a');

		// Replace image with new SVG
		$img.replaceWith($svg);

	}, 'xml');
});

$('#modalW').on("click", "#submit", function () {
	var phone;
	var e = $(this).closest(".forma");
	btn = $(this);
	p_btn = btn.find("p");
	name = e.find('input[name="name"]').val();
	mail = e.find('input[name="mail"]').val();
	phone_el = e.find('input[name="phone"]');
	phone = phone_el.val();
	textarea = e.find('textarea').val();
	console.log(name, mail, phone, textarea)
	ch = "Форма в шапке\n";
	if (phone.length < 18) {
		phone_el.addClass("invalid-phone");
		phone = phone_el.val();
		phone_el.val('Введите номер корректно');
		phone_el.css('color', 'red');
		console.log(phone);
		btn.prop("disabled", true);
		setTimeout(function () {
			phone_el.removeClass("invalid-phone");
			phone_el.val(phone);
			phone_el.css('color', 'black');
			btn.prop("disabled", false);
		}, 2000)
	}
	else {
		$.ajax({
			url: "/api/new.php",
			type: "post",
			dataType: "json",
			data: {
				name: name,
				phone: phone,
				mail: mail,
				ch: ch,
				textarea: textarea
			},
			beforeSend: function () {
				p_btn.html("Идёт отправка...")
			},
			success: function () {
				setTimeout(function () {
					p_btn.text("Отправлено! :)");
					btn.prop("disabled", false);
					e.find('input[name="name"]').val('');
					e.find('input[name="phone"]').val('');
					e.find('input[name="mail"]').val('');
					e.find('textarea').val('');
					setTimeout(function () {
						p_btn.html("Оставить заявку");
					}, 1200);
				}, 1200);
			}
		})
	}
});

